import { usePluginData } from '@wp-plugins/services';
import type { WPTelegramProImData } from './types';

export const useData = <K extends keyof WPTelegramProImData | undefined = undefined>(
	key?: K
): K extends undefined ? WPTelegramProImData : WPTelegramProImData[K] => {
	return usePluginData('wptelegram_pro', key);
};
