import { useCallback, useRef } from 'react';

import {
	AlertDialog,
	AlertDialogBody,
	AlertDialogFooter,
	AlertDialogHeader,
	AlertDialogContent,
	AlertDialogCloseButton,
	AlertDialogOverlay,
	Button,
	ButtonProps,
	useDisclosure,
} from '@wp-plugins/adapters';
import { __ } from '@wp-plugins/i18n';

export interface ConfirmDialogProps {
	body: string;
	cancelText?: string;
	confirmText?: string;
	onConfirm?: VoidFunction;
	title: string;
	triggerProps: ButtonProps;
}

export const ConfirmDialog: React.FC<ConfirmDialogProps> = ({
	body,
	cancelText,
	confirmText,
	onConfirm,
	title,
	triggerProps,
}) => {
	const { isOpen, onOpen, onClose } = useDisclosure();
	const cancelRef = useRef();

	const onClickConfirm = useCallback(() => {
		onClose();
		onConfirm();
	}, [onClose, onConfirm]);

	return (
		<>
			<Button {...triggerProps} onClick={onOpen} />
			<AlertDialog leastDestructiveRef={cancelRef} onClose={onClose} isOpen={isOpen} isCentered>
				<AlertDialogOverlay />
				<AlertDialogContent>
					<AlertDialogHeader>{title}</AlertDialogHeader>
					<AlertDialogCloseButton />
					<AlertDialogBody>{body}</AlertDialogBody>
					<AlertDialogFooter>
						<Button ref={cancelRef} onClick={onClose}>
							{cancelText || __('No')}
						</Button>
						<Button ml={3} onClick={onClickConfirm} colorScheme='blue'>
							{confirmText || __('Yes')}
						</Button>
					</AlertDialogFooter>
				</AlertDialogContent>
			</AlertDialog>
		</>
	);
};
