import { __ } from '@wp-plugins/i18n';
import { FormField } from '@wp-plugins/form';

import { getFieldLabel } from '../services';

const getMessageTypeOptions = () => [
	{
		value: 'sendAudio',
		label: __('Audio'),
	},
	{
		value: 'sendDocument',
		label: __('Document'),
	},
	{
		value: 'sendMessage',
		label: __('Text'),
	},
	{
		value: 'sendPhoto',
		label: __('Photo'),
	},
	{
		value: 'sendVideo',
		label: __('Video'),
	},
];

export const MessageType: React.FC = () => {
	return (
		<FormField
			name='type'
			fieldType='select'
			label={getFieldLabel('type')}
			defaultValue='sendMessage'
			options={getMessageTypeOptions()}
		/>
	);
};
