import { useCallback } from 'react';

import { BaseApiUtilArgs, fetchAPI } from '@wp-plugins/services';

interface FetchUsersArgs extends BaseApiUtilArgs {
	exclude?: Array<number>;
	search?: string;
	per_page?: number;
	telegram_users_only?: boolean;
}

export type FetchUsers = (args: FetchUsersArgs) => Promise<any>;

export const useFetchUsers = (): FetchUsers => {
	const path = 'wp/v2/users/';

	return useCallback(
		async (args) => {
			const { setInProgress, setResult, search, per_page, telegram_users_only } = args;

			setInProgress(true);

			try {
				const data = { per_page, telegram_users_only, search: search || '' };
				// convert params to URL query string
				const urlParams = new URLSearchParams(data as any);
				const pathWithParams = `${path}?${urlParams.toString()}`;

				const result = await fetchAPI.GET({ path: pathWithParams });

				setResult(result);

				return result;
			} catch (error) {
				console.log('ERROR', error);

				setResult([]);

				return [];
			} finally {
				setInProgress(false);
			}
		},
		[path]
	);
};
