import { useCallback, useEffect } from 'react';

import { __ } from '@wp-plugins/i18n';
import { Button } from '@wp-plugins/adapters';
import { FormField, useFormContext, FieldConditions } from '@wp-plugins/form';

import { getFieldLabel, DataShape, useData } from '../services';

let wpMedia: any;

function initWpMedia() {
	wpMedia = window?.wp?.media({
		title: __('Select file'),
		button: {
			text: __('Select'),
		},
		multiple: false,
	});
}

const openMediaModal = () => {
	wpMedia?.open();
};
const conditions: FieldConditions = [
	{
		field: 'type',
		value: 'sendMessage',
		compare: '!=',
	},
];
export const FileURL: React.FC = () => {
	const { send_files_by_url } = useData('uiData');
	const { setValue } = useFormContext<DataShape>();

	const mediaHandler = useCallback(() => {
		const { filepath, url } = wpMedia?.state()?.get('selection')?.first()?.toJSON() || {};

		const attachmentSrc = !send_files_by_url && filepath ? filepath : url;

		setValue('file_url', attachmentSrc);
	}, [send_files_by_url, setValue]);

	useEffect(() => {
		if (!wpMedia) {
			initWpMedia();
		}
	}, []);

	useEffect(() => {
		// de-register the previous listener.
		wpMedia?.off('select', mediaHandler);

		wpMedia?.on('select', mediaHandler);

		return () => wpMedia?.off('select', mediaHandler);
	}, [mediaHandler]);

	const button = <Button onClick={openMediaModal}>{__('Select')}</Button>;

	return (
		<FormField
			name='file_url'
			addonAfter={button}
			fieldType='text.button'
			label={getFieldLabel('file_url')}
			conditions={conditions}
			isRequired
		/>
	);
};
