import { forwardRef } from 'react';
import ReactSelectAsync, { AsyncProps } from 'react-select/async';

import { components } from './components';

type Option = { label: string; value: string };

export type SelectAsyncProps = AsyncProps<Option, false, any>;

export const SelectAsync = forwardRef<ReactSelectAsync, SelectAsyncProps>((props, ref) => {
	return <ReactSelectAsync classNamePrefix='react-select' components={components} {...props} ref={ref as any} />;
});
