import { useCallback } from 'react';
import { filter, map, split, prop, objOf, uniqBy } from 'ramda';

import { useFormContext } from '@wp-plugins/form';
import { fixChatId } from '@wp-plugins/utilities';

import type { ChatIds } from '@wp-plugins/wptelegram-shared--core-pro';

import type { DataShape } from './types';

type ManageChatIds = (chatIds: string) => void;

export const useManageChatIds = (removeIfExists = true): ManageChatIds => {
	const { getValues, setValue } = useFormContext<DataShape>();

	return useCallback(
		(chatIds) => {
			if (!chatIds) {
				return;
			}
			const newChatIdsStr = filter(Boolean, map(fixChatId, split(',', chatIds)));
			if (!newChatIdsStr.length) {
				return;
			}

			const newChatIdsObj: ChatIds = map(objOf('value'), newChatIdsStr);
			const oldChatIdsObj = getValues()?.chat_ids || [];
			const oldChatIdsStr = oldChatIdsObj.map(prop('value'));

			let updatedChatIds = uniqBy(prop('value'), [...oldChatIdsObj, ...newChatIdsObj]);

			if (removeIfExists) {
				updatedChatIds = updatedChatIds.filter(
					({ value }) => !(newChatIdsStr.includes(value) && oldChatIdsStr.includes(value))
				);
			}

			setValue('chat_ids', updatedChatIds);
		},
		[getValues, removeIfExists, setValue]
	);
};
