import { useCallback, useEffect } from 'react';

import { __ } from '@wp-plugins/i18n';
import { FormControl, SelectAsync, SelectAsyncProps } from '@wp-plugins/adapters';
import { useFormContext } from '@wp-plugins/form';
import { useLocalStorage, moduleStorage } from '@wp-plugins/services';

import { DataShape, useData, useManageChatIds } from '../services';
import { CACHE_KEY } from './constants';

const loadingMessage = () => __('Loading...');
const noOptionsMessage = () => __('No options available');

export const SelectP2tgChannels: React.FC = () => {
	const { p2tg_channels } = useData('uiData');
	const { setItem } = useLocalStorage(CACHE_KEY, {}, moduleStorage);

	const { getValues } = useFormContext<DataShape>();
	const manageChatIds = useManageChatIds();

	const onChange = useCallback<SelectAsyncProps['onChange']>(
		(option, action) => {
			if (action.action === 'select-option') {
				const newChatId: string = option.value;
				manageChatIds(newChatId);
			}
		},
		[manageChatIds]
	);

	const isOptionSelected = useCallback(
		(option) => getValues()?.chat_ids?.some(({ value }) => value === option.value),
		[getValues]
	);

	useEffect(() => {
		p2tg_channels.forEach(({ value, label }) => {
			// add to cache
			setItem(value as string, label);
		});
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	return (
		<FormControl mb='2em'>
			<SelectAsync
				classNamePrefix='select-channel'
				closeMenuOnSelect={false}
				defaultOptions={p2tg_channels}
				isOptionSelected={isOptionSelected}
				options={p2tg_channels}
				loadingMessage={loadingMessage}
				noOptionsMessage={noOptionsMessage}
				onChange={onChange}
				placeholder={__('Select from Post to Telegram...')}
				value={null}
			/>
		</FormControl>
	);
};
