import { useMemo } from 'react';

import { __ } from '@wp-plugins/i18n';
import { useFormState, useWatch } from '@wp-plugins/form';
import { ConfirmDialog } from '@wp-plugins/components';
import type { ButtonProps } from '@wp-plugins/adapters';
import { ROOT_ID } from '../constants';

const submit = () => {
	const submitButton = document.getElementById(`${ROOT_ID}-submit`);
	submitButton.click();
};

export const SubmitInfo: React.FC<ButtonProps> = ({ form }) => {
	const { isSubmitting, isDirty } = useFormState();
	const [bot, file_url, text, chat_ids, type] = useWatch({
		name: ['bot', 'file_url', 'text', 'chat_ids', 'type'],
	});

	const triggerProps = useMemo(() => {
		const isDisabled =
			!isDirty ||
			!bot ||
			(type === 'sendMessage' && !text) ||
			(type !== 'sendMessage' && !file_url) ||
			!chat_ids?.length;
		return {
			isDisabled,
			isLoading: isSubmitting,
			children: __('Send'),
		};
	}, [bot, chat_ids?.length, file_url, isDirty, isSubmitting, text, type]);

	return (
		<>
			<ConfirmDialog
				title={__('Send message?')}
				body={__('Are you sure you want to send the message?')}
				onConfirm={submit}
				triggerProps={triggerProps}
			/>
			<input type='submit' form={form} id={`${ROOT_ID}-submit`} className='hidden' />
		</>
	);
};
