import { FormField, useWatch } from '@wp-plugins/form';

import { ParseModeField } from '@wp-plugins/wptelegram-shared--core-pro';

import { getFieldLabel, DataShape } from '../services';

export const MessageText: React.FC = () => {
	const messageType = useWatch<DataShape, 'type'>({ name: 'type' as const });

	return (
		<>
			<FormField
				name='text'
				fieldType='textarea'
				label={getFieldLabel('text')}
				isRequired={messageType === 'sendMessage'}
			/>
			<ParseModeField />
		</>
	);
};
