import { useCallback, useState } from 'react';

import { __ } from '@wp-plugins/i18n';
import { Box, FormControl, SelectAsync, SelectAsyncProps } from '@wp-plugins/adapters';
import { useFormContext } from '@wp-plugins/form';
import { useLocalStorage, moduleStorage } from '@wp-plugins/services';

import { useFetchUsers, DataShape, useManageChatIds } from '../services';
import { CACHE_KEY } from './constants';

const loadingMessage = () => __('Loading...');
const noOptionsMessage = () => __('No options available');

export const SelectUsers: React.FC = () => {
	const [isFetchingUsers, setIsFetchingUsers] = useState(false);

	const { getValues } = useFormContext<DataShape>();

	const { setItem } = useLocalStorage(CACHE_KEY, {}, moduleStorage);
	const fetchUsers = useFetchUsers();
	const manageChatIds = useManageChatIds();

	const onChange = useCallback<SelectAsyncProps['onChange']>(
		(option, action) => {
			if (action.action === 'select-option') {
				const newChatId = option.value;
				manageChatIds(newChatId);
			}
		},
		[manageChatIds]
	);

	const loadOptions = useCallback(
		(search, callback) => {
			fetchUsers({
				search,
				per_page: 20,
				telegram_users_only: true,
				setInProgress: setIsFetchingUsers,
				setResult: (result) => {
					const options =
						result?.map(({ wptelegram_user_id, name }) => {
							// add users to cache
							setItem(wptelegram_user_id, name);
							return {
								value: wptelegram_user_id,
								label: name,
							};
						}) || [];

					callback(options);
				},
			});
		},
		[fetchUsers, setItem]
	);

	const isOptionSelected = useCallback(
		(option) => getValues()?.chat_ids?.some(({ value }) => value === option.value),
		[getValues]
	);

	return (
		<FormControl>
			<SelectAsync
				classNamePrefix='select-user'
				closeMenuOnSelect={false}
				defaultOptions
				isLoading={isFetchingUsers}
				isOptionSelected={isOptionSelected}
				loadOptions={loadOptions}
				loadingMessage={loadingMessage}
				noOptionsMessage={noOptionsMessage}
				onChange={onChange}
				placeholder={__('Select user...')}
				value={null}
			/>
			<Box textAlign='center' my='1em'>
				{__('OR')}
			</Box>
		</FormControl>
	);
};
