import { useCallback } from 'react';
import { find, propEq } from 'ramda';

import { __ } from '@wp-plugins/i18n';
import type { SubmitHandler } from '@wp-plugins/form';
import { TestResult, TestResultType, getErrorMessage, botApi } from '@wp-plugins/services';
import type { BotInstance } from '@wp-plugins/utilities';

import type { DataShape } from './types';
import { useData } from './useData';

type SetStateAction<S> = React.Dispatch<React.SetStateAction<S>>;

type SubmitForm = (
	setMessageResult: SetStateAction<TestResult>,
	setMessageResultType: SetStateAction<TestResultType>
) => SubmitHandler<DataShape>;

const getBotToken = (bot_username: string, collection: Array<BotInstance>) => {
	return find<BotInstance>(propEq('bot_username', bot_username), collection)?.bot_token;
};

export const useSubmitForm: SubmitForm = (setMessageResult, setMessageResultType) => {
	const { bot_options } = useData('uiData');

	return useCallback(
		async (data) => {
			const bot_token = getBotToken(data.bot, bot_options);
			const { chat_ids, text, type, parse_mode, file_url } = data;
			setMessageResult({});
			setMessageResultType({});

			botApi.setBotToken(bot_token);

			await Promise.all(
				chat_ids.map(async ({ value: chat_id }) => {
					try {
						// convert "sendDocument" to "document"
						const mediaKey = type.substring(4).toLowerCase();
						const textKey = 'message' === mediaKey ? 'text' : 'caption';

						await botApi[type]({ chat_id, [mediaKey]: file_url, [textKey]: text, parse_mode });

						setMessageResultType((prevResultType) => ({
							...prevResultType,
							[chat_id]: 'SUCCESS',
						}));

						setMessageResult((prevResult) => ({
							...prevResult,
							[chat_id]: __('Success'),
						}));
					} catch (error) {
						console.log('ERROR', error);
						setMessageResultType((prevResultType) => ({
							...prevResultType,
							[chat_id]: 'ERROR',
						}));

						const result = getErrorMessage(error);

						setMessageResult((prevResult) => ({
							...prevResult,
							[chat_id]: result,
						}));
					}
				})
			);
		},
		[bot_options, setMessageResult, setMessageResultType]
	);
};
