import { Fragment, useMemo } from 'react';

import { __ } from '@wp-plugins/i18n';
import { Tag, TagLabel, TagCloseButton, Stack } from '@wp-plugins/adapters';
import { useFieldArray, FormField } from '@wp-plugins/form';
import { useLocalStorage, moduleStorage } from '@wp-plugins/services';

import { CACHE_KEY } from './constants';
import { useData, DataShape } from '../services';

export const ChatIdOutput: React.FC = () => {
	const { p2tg_channels } = useData('uiData');
	const chatIdsArray = useFieldArray<DataShape, 'chat_ids'>({
		name: 'chat_ids' as const,
	});

	const channelsObject = useMemo(() => {
		return p2tg_channels.reduce((obj, { value, label }) => ({ ...obj, [value]: label }), {});
	}, [p2tg_channels]);

	const { getItem } = useLocalStorage(CACHE_KEY, {}, moduleStorage);

	return (
		<Stack spacing={4} isInline flexWrap='wrap'>
			<span>{__('Send to:')}</span>
			{chatIdsArray.fields.map((chatId, index) => {
				const chat_id = chatId.value;
				const labelFromUserCache = getItem<string>(chat_id, '');

				const label = labelFromUserCache
					? chat_id in channelsObject && labelFromUserCache.includes(chat_id)
						? labelFromUserCache
						: `${labelFromUserCache} (${chat_id})`
					: chat_id;

				const colorScheme = chat_id in channelsObject ? 'blue' : labelFromUserCache ? 'teal' : 'gray';

				return (
					<Fragment key={chatId.id}>
						<Tag variant='outline' colorScheme={colorScheme} className='chat-id-tag'>
							<TagLabel>{label}</TagLabel>
							<TagCloseButton
								// eslint-disable-next-line react-perf/jsx-no-new-function-as-prop
								onClick={() => chatIdsArray.remove(index)}
							/>
						</Tag>
						<FormField name={`chat_ids.${index}.value`} defaultValue={chat_id} fieldType='hidden' />
					</Fragment>
				);
			})}
		</Stack>
	);
};
