import React, { useCallback, useState, useRef } from 'react';

import {
	Box,
	Button,
	Input,
	InputGroup,
	InputRightAddon,
	FormControl,
	FormHelperText,
	InputProps,
} from '@wp-plugins/adapters';
import { __ } from '@wp-plugins/i18n';

import { useManageChatIds } from '../services';

export const ChatIdInput: React.FC = () => {
	const [chatIds, setChatIds] = useState('');

	const inputRef = useRef<HTMLInputElement>();

	const onChange = useCallback<InputProps['onChange']>((event) => setChatIds(event.target.value), []);

	const manageChatIds = useManageChatIds(false);

	const onAdd = useCallback(() => {
		if (!chatIds) {
			return;
		}
		manageChatIds(chatIds);
		setChatIds('');
		inputRef.current?.focus();
	}, [chatIds, manageChatIds]);

	const onKeyPress = useCallback<InputProps['onKeyPress']>(
		(event) => {
			if ('Enter' === event.key) {
				event.preventDefault();
				onAdd();
			}
		},
		[onAdd]
	);

	return (
		<FormControl>
			<InputGroup borderColor='gray.500'>
				<Input
					borderLeftWidth='5px'
					borderLeftRadius={null}
					borderRight='0'
					borderRightRadius='0'
					id='add-chat-id'
					isInvalid={false}
					onChange={onChange}
					onKeyPress={onKeyPress}
					placeholder={__('Enter Chat ID or channel username')}
					ref={inputRef}
					value={chatIds}
				/>
				<InputRightAddon className='addon' px='0'>
					<Button onClick={onAdd} isDisabled={!chatIds}>
						{__('Add')}
					</Button>
				</InputRightAddon>
			</InputGroup>
			<FormHelperText id='add-chat-id-helper-text'>
				{__('If more than one, separate them by comma')}
			</FormHelperText>
			<Box textAlign='center' my='1em'>
				{__('OR')}
			</Box>
		</FormControl>
	);
};
