import { useState } from 'react';

import { Form, useForm, FormDebug } from '@wp-plugins/form';
import { MessageResult, Cols60x40 } from '@wp-plugins/components';
import type { TestResult, TestResultType } from '@wp-plugins/services';
import { isDev } from '@wp-plugins/utilities';

import { SubmitInfo } from './SubmitInfo';
import { ChatIdInput } from './ChatIdInput';
import { SelectUsers } from './SelectUsers';
import { SelectP2tgChannels } from './SelectP2tgChannels';
import { ChatIdOutput } from './ChatIdOutput';
import { MessageType } from './MessageType';
import { MessageText } from './MessageText';
import { FileURL } from './FileURL';
import { BotToUse } from './BotToUse';
import { useSubmitForm, useData, DataShape } from '../services';
import { ROOT_ID } from '../constants';

const defaultValues: DataShape = {
	bot: '',
	type: 'sendMessage',
	chat_ids: [],
	parse_mode: 'none',
	file_url: '',
};

export const Messages: React.FC = () => {
	const [messageResult, setMessageResult] = useState<TestResult>({});
	const [messageResultType, setMessageResultType] = useState<TestResultType>({});

	const onSubmit = useSubmitForm(setMessageResult, setMessageResultType);
	const form = useForm({ defaultValues });
	const { is_wptg_login_active } = useData('uiData');

	const leftCol = (
		<>
			<ChatIdInput />
			{is_wptg_login_active && <SelectUsers />}
			<SelectP2tgChannels />
			<ChatIdOutput />
			<BotToUse />
			<MessageType />
			<FileURL />
			<MessageText />
			<SubmitInfo />
		</>
	);

	const rightCol = (
		<>
			<MessageResult messageResult={messageResult} messageResultType={messageResultType} />
			{isDev && <FormDebug />}
		</>
	);

	return (
		<Form id={`${ROOT_ID}-form`} onSubmit={form.handleSubmit(onSubmit)} form={form}>
			<Cols60x40 maxWidth='1500px' leftCol={leftCol} rightCol={rightCol} />
		</Form>
	);
};
